#root {
  display: flex;
  align-items: center;
  justify-content: center;
}
body {
  margin: 0;
}
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 30px;
  width: 100%;
}
.App .topSection {
  width: 100%;
  color: white;
  background: linear-gradient(90deg, #0b010e 0%, #353635 100%);
  height: 40vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.App .topSection .topContainer {
  width: 70%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 30px;
}
.App .topSection .topContainer .counter {
  font-size: 6rem;
  font-family: 'Orbitron', sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}
.App .topSection .topContainer .timeButtons {
  display: flex;
  width: 60%;
  justify-content: space-between;
}
.App .topSection .topContainer .timeButtons .button {
  border: solid 1.5px white;
  padding: 5px;
  border-radius: 10px;
  font-size: 1rem;
  font-family: 'Orbitron', sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  display: flex;
  gap: 5px;
  position: relative;
  z-index: 1;
  overflow: hidden;
  transition: 0.42s ease-in-out;
  animation: fadeIn 0s ease-in-out 0s forwards;
  opacity: 0;
}
.start:hover img {
  content: url('./playb.png');
}
.stop:hover img {
  content: url('./pauseb.png');
}

.reset:hover img {
  content: url('./resetb.png');
}
.lapButton:hover img {
  content: url('./lapb.png');
}
.App .topSection .topContainer .timeButtons .button:hover {
  color: rgb(27, 26, 26);
  transform: scale(1.1);
  border: none;
}
.App .topSection .topContainer .timeButtons .button:hover::before {
  width: 200%;
  height: 300%;
  border-radius: 100%;
}
.App .topSection .topContainer .timeButtons .button::before {
  content: '';
  background-color: #faf8f8;
  position: absolute;
  width: 0%;
  bottom: 0;
  left: 50%;
  height: 0%;
  transition: 0.42s ease-in-out;
  z-index: -1;
  transform: translateX(-50%) translateY(50%);
}
.App .topSection .topContainer .timeButtons .button img {
  width: 20px;
}
.App .topSection .topContainer .startStop {
  display: flex;
  gap: 15px;
}
.App .bottomSection {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 30px;
}
.App .bottomSection .lapContainer {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 75%;
  font-family: 'Nova Square', sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 1.2rem;
  transition: 0.42s ease-in-out;
}
.App .bottomSection .lapContainer:hover {
  transform: scale(1.05);
}
.App .bottomSection .lapContainer .lapCount {
  opacity: 0.5;
}
.minLap {
  color: green;
}
.maxLap {
  color: red;
}
.noLap {
  display: flex;
  height: 50vh;
  width: 80%;
  justify-content: center;
}
.loader {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  height: 100vh;
  width: 100%;
  transition: opacity 1s ease-out;
}

.svg-wrapper {
  width: 50%;
  height: 50%;
}

@media (max-width: 900px) {
  .App .topSection .topContainer .counter {
    font-size: 4rem;
  }
}
@media (max-width: 900px) {
  .App .topSection .topContainer {
    width: 100%;
  }
  .App .topSection .topContainer .counter {
    font-size: 3rem;
  }
  .App .bottomSection .lapContainer {
    width: 100%;
  }
  .App .topSection .topContainer .timeButtons {
    width: 80%;
  }
  .App .topSection .topContainer .timeButtons .button {
    border: solid 1.5px white;
    padding: 5px;
    border-radius: 10px;
    font-size: 0.8rem;
  }
  .App .topSection .topContainer .timeButtons .button img {
    width: 15px;
  }
}
